<template>
  <b-container id="header">
          <img class="col-5 col-md-3 col-lg-1 col-xl-1" alt="Vue logo" src="../assets/logo.png" />
    <div class="fixed-top row flex-row-reverse ml-auto pt-3 pr-3">
      <b-icon-justify class="greenIcon mx-3" font-scale="2" v-b-toggle.navBar>
      </b-icon-justify>
      <div @click="toNotifications">
        <b-icon-bell-fill class="greenIcon" font-scale="2">
        </b-icon-bell-fill>
        <b-badge class="notifiCounter rounded" variant="light">
          {{ notificationNumber }}
        </b-badge>
      </div>
    </div>
    <b-sidebar id="navBar" right shadow>
      <b-nav vertical>
                    <router-link v-for="(link, index) in links" :key="index"
                    :to="link.route" class="my-2">
                         {{link.name}}
                    </router-link>
      </b-nav>
      <template v-slot:footer>
        <div class="pb-2 d-flex align-items-center justify-content-center">
          <b-avatar variant="light"
                         :src="avatarLink"></b-avatar>
                         <div class="mx-3">{{login}}</div>
                         <b-icon-arrow-right-square font-scale="2" @click="logOut">
                         </b-icon-arrow-right-square>
        </div>
      </template>
    </b-sidebar>
  </b-container>
</template>

<script>
export default {
  name: 'HeadBar',
  data() {
    return {
      links: [],
      avatarLink: '',
      login: '',
    };
  },
  props: {
    linksInput: {
      type: Array,
      required: true,
    },
  },
  computed: {
    notificationNumber() {
      return this.$store.getters.numberOfNewNotifications;
    },
  },
  mounted() {
    this.links = this.linksInput;
    this.login = this.$store.getters.login;
    this.avatarLink = `${process.env.VUE_APP_API_BASE_URL}user/avatar/${this.$store.getters.userId}`;
  },
  methods: {
    logOut() {
      this.$store.dispatch('loginOut').then(() => {
        window.location.href = '/login.html';
      });
    },
    toNotifications() {
      this.$router.push({ path: '/notifications' });
    },
  },
};
</script>

