  padding: 30px;

     .greenIcon{
          color: #148A14;
     }
  .notifiCounter{
    position: relative;
    right: 10px;
  }
  #navBar{
       background-color: #1C1919 !important;
       height: 100% !important;

       a {
          font-weight: bold;
